import { useState } from "react";

import { Link } from "react-router-dom";
import logo from "../images/logo.png";
import "./Navbar.css";
import { BiMenu, BiCaretDown } from "react-icons/bi";
import { MdClose } from "react-icons/md";

const Navbar = ({ children }) => {
	const [menuOpen, setMenuOpen] = useState(false);
	const [dropDown, setDropDown] = useState(false);
	const openMenu = () => {
		setMenuOpen(!menuOpen);
	};

	const openDropDown = () => {
		setDropDown(!dropDown);
	};
	return (
		<div>
			<nav className={menuOpen ? "nav navOpen" : "nav"}>
				<div
					className={
						menuOpen ? "navContainer navOpen" : "navContainer"
					}
				>
					<img src={logo} className="logo" alt="logo" />
					<div className="ul">
						<Link
							className="a"
							to="/"
							onClick={() => setDropDown(false)}
						>
							Home
						</Link>
						<div className="dropDown">
							<Link
								className="a"
								// to="/work"
								onClick={() => openDropDown()}
							>
								<div className="chevContainer">
									My Work <BiCaretDown className="chev" />
								</div>
							</Link>
							<div
								className={
									dropDown
										? "dropDownContent showDropDown"
										: "dropDownContent"
								}
							>
								<Link onClick={() => openDropDown()}>
									Creative Photography
								</Link>
								<Link onClick={() => openDropDown()}>
									Wedding Photography
								</Link>
								<Link onClick={() => openDropDown()}>
									Portrait Photography
								</Link>
								<Link onClick={() => openDropDown()}>
									Childern Photography
								</Link>
							</div>
						</div>

						<Link
							className={
								dropDown
									? "hiddenDropDown showHiddenDropDown"
									: "hiddenDropDown"
							}
							onClick={() => openDropDown()}
						>
							Creative Photography
						</Link>
						<Link
							className={
								dropDown
									? "hiddenDropDown showHiddenDropDown"
									: "hiddenDropDown"
							}
							onClick={() => openDropDown()}
						>
							Wedding Photography
						</Link>
						<Link
							className={
								dropDown
									? "hiddenDropDown showHiddenDropDown"
									: "hiddenDropDown"
							}
							onClick={() => openDropDown()}
						>
							Portrait Photography
						</Link>
						<Link
							className={
								dropDown
									? "hiddenDropDown showHiddenDropDown"
									: "hiddenDropDown"
							}
							onClick={() => openDropDown()}
						>
							Children Photography
						</Link>

						<Link
							className="a"
							to="/contact"
							onClick={() => setDropDown(false)}
						>
							Contact
						</Link>
					</div>

					{(!menuOpen && (
						<BiMenu className="menu" onClick={() => openMenu()} />
					)) || (
						<MdClose
							className="menu menuClose"
							onClick={() => openMenu()}
						/>
					)}
				</div>
			</nav>
		</div>
	);
};

export default Navbar;
